import { IExposedProperty } from '@/models/property';
import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'ir-facilities',
  styleUrl: 'ir-facilities.css',
  shadow: true,
})
export class IrFacilities {
  @Prop() properties: IExposedProperty;
  render() {
    if (!this.properties) {
      return null;
    }
    return (
      <Host>
        <div class="bg-gray-50 rounded-md p-4 space-y-5">
          <div class="flex  items-center gap-4">
            <ir-icons name="clock"></ir-icons>
            <p>
              Check-in: from {this.properties?.time_constraints.check_in_from} until {this.properties?.time_constraints.check_in_till}
            </p>
            <p>Check-out:{this.properties?.time_constraints.check_out_till}</p>
          </div>
          <div class="flex items-center gap-4">
            <ir-icons name="wifi"></ir-icons>
            <p>
              Public areas: <span class="text-green-500">{this.properties?.internet_offering.public_internet_statement}</span>
            </p>
            <p>
              Rooms: <span class="text-green-500">{this.properties?.internet_offering.is_room_internet_free ? 'Free Internet' : 'Paid Internet'}</span>
            </p>
          </div>
          <div class="flex items-center gap-4">
            <ir-icons name="car"></ir-icons>
            <p>
              {this.properties?.parking_offering.title} at {this.properties?.parking_offering.pricing}
            </p>
          </div>
          <div class="flex items-center gap-4">
            <ir-icons name="pets"></ir-icons>
            <p>{this.properties?.pets_acceptance.title}</p>
          </div>
          <div class="flex items-center gap-4">
            <ir-icons name="bed"></ir-icons>
            <p>{this.properties?.baby_cot_offering.title}</p>
          </div>
          <div class="flex flex-col md:flex-row md:items-start gap-4 flex-wrap">
            {/* hotel */}
            <div class="flex gap-4">
              <ir-icons name="home"></ir-icons>
              <ul>
                <li class="font-medium">Property facilies</li>
                {this.properties.amenities.map(aminity => {
                  if (aminity.amenity_type !== 'property') {
                    return null;
                  }
                  return <li key={aminity.code}>{aminity.description}</li>;
                })}
              </ul>
            </div>
            <div class="flex gap-4">
              <ir-icons name="football"></ir-icons>
              <ul>
                <li class="font-medium">Activities</li>
                {this.properties.amenities.map(aminity => {
                  if (aminity.amenity_type !== 'activity') {
                    return null;
                  }
                  return <li key={aminity.code}>{aminity.description}</li>;
                })}
              </ul>
            </div>
            <div class="flex gap-4">
              <ir-icons name="bell"></ir-icons>
              <ul>
                <li class="font-medium">Services</li>
                {this.properties.amenities.map(aminity => {
                  if (aminity.amenity_type !== 'service') {
                    return null;
                  }
                  return <li key={aminity.code}>{aminity.description}</li>;
                })}
              </ul>
            </div>
          </div>
          <div class="flex items-center gap-4">
            {/* utencils */}
            <ir-icons name="utencils"></ir-icons>
            <p>
              <span class="font-medium">Food and beverage: </span>
              {this.properties.description.food_and_beverage}
            </p>
          </div>
          <div class="flex items-center gap-4">
            {/* credit card */}
            <ir-icons name="credit_card"></ir-icons>
            <p>
              <span class="font-medium">Accepted credit cards at the property: </span>
              {this.properties?.allowed_cards.map((card, index) => {
                return (
                  <span key={card.id}>
                    {card.name}
                    {index < this.properties.allowed_cards.length - 1 && <span> - </span>}
                  </span>
                );
              })}
            </p>
          </div>
          {/* TODO: add the cancelation and guarentee */}
          {/* <div class="flex items-center gap-4">
           
            <ir-icons name="check"></ir-icons>
            <div>
             <p innerHTML=''></p>
            </div>
          </div> */}
          {this.properties?.description.important_info && (
            <div class="flex items-center gap-4">
              {/* danger */}
              <ir-icons name="danger" svgClassName="text-red-500"></ir-icons>
              <div>
                <p>{this.properties?.description.important_info}</p>
                <p>{this.properties?.description.non_standard_conditions}</p>
              </div>
            </div>
          )}
        </div>
      </Host>
    );
  }
}
